import { IonActionSheet, IonCard, IonCardHeader, IonCardSubtitle, IonCardTitle, IonCol, IonContent, IonFab, IonFabButton, IonGrid, IonIcon, IonPage, IonRow } from "@ionic/react"
import React, { useContext, useEffect, useState } from "react"

import { add, caretForwardCircle, close, heart, share, trash } from 'ionicons/icons';
import { useHistory } from "react-router";
import { appFormContext } from "../../context/AppForms";
import { newToast } from "../../components/newToast";

export const Home: React.FC = () => {

    const { getList, list, create } = useContext(appFormContext)

    const [showActionSheet, setShowActionSheet] = useState(false);

    const [IdCurrent, setIdCurrent] = useState("");

    const { push } = useHistory()

    const HandleCreate = async () => {
        try {

            push("/app/modify/" + await create())
            await getList();
        } catch (error) {

            newToast(error)

        }
    }

    const HandleUpdate = async () => {


        push("/app/modify/" + IdCurrent)

    }

    useEffect(() => {
        getList();
    }, [getList])

    return (
        <IonPage>
            <IonContent fullscreen className="ion-padding" >
                <IonGrid>
                    <IonRow className="ion-justify-content-space-arrow ion-padding ">
                        {
                            list.map((_, index) => {
                                return (
                                    <IonCol sizeMd="1" key={index}>
                                        <IonCard >
                                            {/* <div ></div> */}
                                            <IonFab vertical="top" horizontal="end" slot="absolute">
                                                <span>8</span>
                                            </IonFab>
                                            <IonCardHeader
                                                onClick={() => {

                                                    setShowActionSheet(true);

                                                    setIdCurrent(_._id);
                                                }}>
                                                <IonCardTitle>{_.name}</IonCardTitle>
                                                <IonCardSubtitle> </IonCardSubtitle>
                                            </IonCardHeader>
                                        </IonCard>
                                    </IonCol>
                                )
                            })
                        }
                    </IonRow>
                </IonGrid>
                <IonActionSheet
                    isOpen={showActionSheet}
                    onDidDismiss={() => setShowActionSheet(false)}
                    cssClass='my-custom-class'
                    buttons={[{
                        text: 'Delete',
                        role: 'destructive',
                        icon: trash,
                        handler: () => {
                            console.log('Delete clicked');
                        }
                    }, {
                        text: 'Share',
                        icon: share,
                        handler: () => {
                            console.log('Share clicked');
                        }
                    }, {
                        text: 'Open',
                        icon: caretForwardCircle,
                        handler: HandleUpdate
                    }, {
                        text: 'Favorite',
                        icon: heart,
                        handler: () => {
                            console.log('Favorite clicked');
                        }
                    }, {
                        text: 'Cancel',
                        icon: close,
                        role: 'cancel',
                        handler: () => {
                            console.log('Cancel clicked');
                        }
                    }]}
                >
                </IonActionSheet>
                <IonFab vertical="bottom" horizontal="end" slot="fixed">
                    <IonFabButton onClick={HandleCreate}>
                        <IonIcon icon={add} />
                    </IonFabButton>
                </IonFab>
            </IonContent>
        </IonPage>
    )
}