import { IonBackButton, IonButtons, IonContent, IonHeader, IonItem, IonLabel, IonList, IonModal, IonPage, IonTitle, IonToolbar } from "@ionic/react"
import React, { useContext, useEffect } from "react"
import { useParams } from "react-router"
import { appFormContext } from "../../context/AppForms"

class Validacion extends React.Component<{ id: string }> {
    state = {
        va: false
    }
    componentDidMount() {
        const { validate } = this.context
        validate(this.props.id, (respuesta: boolean) => {
            this.setState({ va: respuesta })
        })
    }

    render() {

        if (this.state.va) {
            return this.props.children
        } else {
            return <div >
                ...cargando
            </div>
        }
    }
}
Validacion.contextType = appFormContext;

export const Modify: React.FC = () => {

    const { id } = useParams<{ id: string }>();

    const { listQuestion, getListQuestion, clearQuestion } = useContext(appFormContext);

    useEffect(() => {
        getListQuestion(id)
        return () => {
            clearQuestion()
        }
    }, [id, getListQuestion, clearQuestion])

    return (
        <IonModal isOpen={true}>
            <IonPage >
                <IonHeader>
                    <IonToolbar >
                        <IonTitle>Modal Agregar</IonTitle>
                        <IonButtons slot="start">
                            <IonBackButton defaultHref="/app/home" />
                        </IonButtons>
                    </IonToolbar>
                </IonHeader>
                <IonContent className="ion-padding">
                    <Validacion id={id}>
                        <IonList>
                            {
                                listQuestion.map((value, index) =>
                                    (
                                        <IonItem key={index}>
                                            <IonLabel>{JSON.stringify(value)}</IonLabel>
                                        </IonItem>
                                    )
                                )
                            }
                            {
                                listQuestion.length < 1 && (
                                    <IonItem>
                                        <IonLabel>no tiene preguntas</IonLabel>
                                    </IonItem>
                                )
                            }
                        </IonList>

                    </Validacion>
                </IonContent>
            </IonPage>
        </IonModal>
    )
}